import React from 'react';
import './InstructionsModal.css';
import useCards from '../../hooks/useCards';

interface InstructionsModalProps {
    isOpen: boolean;
    onClose: () => void;
}

export default function InstructionsModal({ isOpen, onClose }: InstructionsModalProps) {
    if (!isOpen) return null;

    const handleBackdropClick = (e: React.MouseEvent) => {
        if (e.target === e.currentTarget) {
            onClose();
        }
    };

    return (
        <div className="instructions-modal-backdrop" onClick={handleBackdropClick}>
            <div className="instructions-modal">
                <div className="instructions-modal-header">
                    <h2>Jak gra?</h2>
                    <button className="instructions-modal-close" onClick={onClose}>
                        
                    </button>
                </div>

                <div className="instructions-modal-content">
                    <div className="instructions-section">
                        <h3> Cel gry</h3>
                        <p>
                            Podejmuj yciowe decyzje i obserwuj jak wpywaj na Twoje statystyki.
                            Celem jest przeycie penego ycia, podejmujc mdre wybory!
                        </p>
                    </div>

                    <div className="instructions-section">
                        <h3> Sterowanie</h3>
                        <ul>
                            <li><strong>Strzaki /</strong> lub <strong>swipe</strong> - przeczanie midzy opcjami</li>
                            <li><strong>Spacja</strong> lub <strong>kliknicie</strong> - wybr opcji</li>
                            <li><strong>Tap na statystykach</strong> - rozwi/zwi szczegy</li>
                            <li><strong>Klawisz H</strong> lub <strong>przycisk ?</strong> - otwrz ponownie instrukcje</li>
                        </ul>
                    </div>

                    <div className="instructions-section">
                        <h3> Mechanika gry</h3>
                        <ul>
                            <li><strong>Zdrowie</strong> - Utrzymuj je wysokie, aby przey</li>
                            <li><strong>Szczcie</strong> - Wpywa na jako ycia</li>
                            <li><strong>Pienidze</strong> - Otwieraj nowe moliwoci</li>
                            <li><strong>Inteligencja</strong> - Pomoe w trudnych sytuacjach</li>
                        </ul>
                    </div>

                    <div className="instructions-section">
                        <h3> Wskazwki</h3>
                        <ul>
                            <li>Kada decyzja ma konsekwencje - myl dugoterminowo</li>
                            <li>Rwnowaga midzy wszystkimi statystykami to klucz do sukcesu</li>
                            <li>Niektre wydarzenia s losowe - to cz ycia!</li>
                            <li>Twj wiek wpywa na dostpne opcje</li>
                        </ul>
                    </div>
                </div>

                <div className="instructions-modal-footer">
                    <button className="instructions-modal-start-btn" onClick={onClose}>
                        Zacznij gr!
                    </button>
                </div>
            </div>
        </div>
    );
}